export default function CustomDomain(props) {
  const [isEditing, setIsEditing] = useState(false);
  const [subdomain, setSubdomain] = useState("");
  const [subdomainItems, setSubdomainItems] = useState([]);
  const [isUpdated, setIsUpdated] = useState(false);
  const [editingValue, setEditingValue] = useState("");
  const [error, setError] = useState("");
  const route = props.id;
  const eventId = props?.openData?.data?._id;

  const cleanSubdomainInput = (value) => {
    return value
      .toLowerCase()
      .replace(/[^a-z0-9-]/g, "") // Remove all characters that aren't lowercase letters, numbers, or hyphens
      .replace(/-+/g, "-") // Replace multiple consecutive hyphens with a single hyphen
      .replace(/^-+|-+$/g, ""); // Remove hyphens from start and end
  };

  const validateSubdomain = (value) => {
    if (!value) {
      setError("Subdomain cannot be empty");
      return { isValid: false, value: "" };
    }

    if (value.length < 1 || value.length > 63) {
      setError("Subdomain must be between 1 and 63 characters");
      return { isValid: false, value };
    }

    // Check if the value starts with a letter/number and ends with a letter/number
    const firstChar = value.charAt(0);
    const lastChar = value.charAt(value.length - 1);
    if (!/[a-z0-9]/.test(firstChar) || !/[a-z0-9]/.test(lastChar)) {
      setError("Subdomain must start and end with a letter or number");
      return { isValid: false, value };
    }

    // Clear error if validation passes
    setError("");
    return { isValid: true, value };
  };

  const handleEditClick = () => {
    if (isEditing) {
      // If canceling edit mode, restore the full domain
      setEditingValue("");
    } else {
      // If entering edit mode, remove .eventhex.ai and set for editing
      const domainParts = subdomain.split(".eventhex.ai");
      setEditingValue(domainParts[0]);
    }
    setIsEditing(!isEditing);
    setError(""); // Clear any errors
  };

  const handleUpdate = () => {
    const validation = validateSubdomain(editingValue);
    // setError(JSON.stringify(validation));
    if (validation.isValid) {
      setSubdomain(`${validation.value}.eventhex.ai`);
      setIsEditing(false);
      setError("");
    }
  };

  useEffect(() => {
    getData({ event: eventId }, "whitelisted-Domains").then((response) => {
      if (response.status === 200) {
        const items = response?.data?.response;
        setSubdomainItems(items);
        setSubdomain(items[0]?.domain ?? "your-event.eventhex.ai");
      }
    });
  }, [eventId, route]);

  return (
    <RowContainer className={"data-layout "}>
      <PageHeader title={"Domain Settings"} line={false} description={"Manage your event's domain settings and custom domain configuration."} />

      <Card>
        <FlexContainer className="control">
          <TextBoxWithButton
            icon={"globe"}
            onClick={handleEditClick}
            onChange={(value) => {
              const cleanedValue = cleanSubdomainInput(value);

              setIsUpdated(`${cleanedValue}.eventhex.ai` !== subdomain);
              setEditingValue(cleanedValue);
              validateSubdomain(cleanedValue);
            }}
            text={isEditing ? "Cancel" : "Edit Subdomain"}
            label="Subdomain"
            value={isEditing ? editingValue : subdomain}
            placeholder="Please add a sub domain!"
            footnote={isEditing ? (error?.length > 0 ? error : `${editingValue}.eventhex.ai`) : ""}
            error={!!error}
            customClass={`full ${isEditing ? "transparent" : "transparent"}`}
          />
          {isEditing && <Button isDisabled={!isUpdated} value="Update" icon={"save"} size={16} ClickEvent={handleUpdate} />}
        </FlexContainer>
      </Card>

      <FlexContainer justify="between">
        <SubPageHeader title={"Custom Domains"} line={false} description={""} />
        <AddButton onClick={() => {}}>
          <GetIcon icon={"add"} />
          <span>Add Domain</span>
        </AddButton>
      </FlexContainer>
      {subdomainItems?.map((item) => (
        <Card>
          <FlexContainer justify="between">
            <FlexContainer>
              <GetIcon icon={"link"} />
              <DomainInfo>
                <div className="h3">{item.domain}</div>
              </DomainInfo>
            </FlexContainer>
            {item.verified ? <StatusBadge className="active">Active</StatusBadge> : <StatusBadge className="pending">Pending</StatusBadge>}
          </FlexContainer>

          <FlexContainer>
            <ConfigurationRow>
              <span>CNAME</span>
              <CopyWrapper>
                <span>cname.eventhex.ai</span>
                <IconButton type="plain" icon={"copy"} size={16} />
              </CopyWrapper>
            </ConfigurationRow>
          </FlexContainer>
        </Card>
      ))}
      {/* {subdomainItems?.length>0 && (
        <ListTable
          // actions={actions}
          api={`whitelisted-Domains`}
          itemTitle={{
            name: "domain",
            type: "text",
            collection: "",
          }}
          attributes={attributes}
          parentReference={"event"}
          referenceId={eventId}
          parents={{ route: props.id, event: eventId }}
          preFilter={{ route: props.id, event: eventId }}
          shortName={`Domain`}
          formMode={`single`}
          preData={subdomainItems}
          // showTitle={false}
          // showFilters={false}
          showPagination={false}
          showHeaderRow={false}
          icon="link"
          viewMode={"list"}
          name={route}
          delPrivilege={true}
          addPrivilege={true}
          updatePrivilege={true}
          {...props}
        ></ListTable>
      )} */}
    </RowContainer>
  );
}
