import React, { useState, useEffect, useRef, useCallback } from "react";
import styled from "styled-components";
import { Rnd } from "react-rnd";
import FormInput from "../../../core/input";
import { Button, ColorPicker, IconButton, NumberBox, Select, SliderBox, TextBox } from "../../../core/elements";
import { Footer, Form } from "../../../core/list/create/styles";
import { PageHeader, SubPageHeader } from "../../../core/input/heading";
import { appTheme } from "../../brand/project";
import NoDataFound from "../../../core/list/nodata";
import { noimage } from "../../../../images";
import { getCustomData, getData, putData } from "../../../../backend/api";
// import html2canvas from "html2canvas";

const Container = styled.div`
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
`;
const StyledRnd = styled(Rnd)`
  &:hover {
    border: 2px dashed blue; /* Show border on hover */
  }
`;

/* Left Side Controls */
const Controls = styled.div`
  width: 30%;
  padding: 0px;
  padding-top: 10px;
  border-left: 1px solid rgb(226 228 232);
  overflow-y: auto;
  gap: 16px;
  display: flex;
  flex-direction: column;

  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
`;

const ControlsBox = styled.div`
  display: flex;
  justify-content: space-between;
`;
const ControlsSection = styled.div`
  /* margin-bottom: 16px; */
  display: flex;
  gap: 10px;
  margin: 20px 0px 0px;
`;
const ItemSettings = styled.div`
  /* margin-bottom: 16px; */
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid ${appTheme.stroke.soft};
  padding: 10px;
  border-radius: 10px;
  margin: 0 10px;
  &.nomargin {
    margin: 0px;
    position: absolute;
    bottom: 0;
    z-index: 1;
    background: white;
    left: 0;
    right: 0;
    border: 0;
    height: 80px;
    border: 1px solid rgb(226, 228, 233);
    position: sticky;
    bottom: 10px;
    top: auto;
    width: max-content;
    margin: auto;
  }
`;

const LayersList = styled.div`
  /* margin-bottom: 16px; */
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const LayerItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  cursor: move;
  border-radius: 10px;
  border: 1px solid rgb(226, 228, 233);
  background-color: ${({ isSelected }) => (isSelected ? "#e0e0e0" : "transparent")};
  &:hover {
    background-color: #f5f5f5;
  }
`;

/* Right Side Preview */
const Preview = styled.div`
  width: 70%;
  padding: 0px 20px 0px;
  position: relative;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow: scroll;
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
`;

const Poster = styled.div`
  width: ${({ width }) => width || 700}px;
  min-height: ${({ height }) => height || 400}px;
  background-color: #f5f5f5;
  /* background-image: url(${({ backgroundImageUrl }) => backgroundImageUrl || "none"}); */
  background-size: cover;
  background-position: center;
  /* border: 1px solid #ccc; */
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  position: relative;
  margin: 0 auto 40px;
  overflow: hidden;
  margin-top: 20px;
`;

/* Text Elements */
const TextElement = styled.div`
  color: ${({ color }) => color || "#000"};
  font-size: ${({ fontSize }) => fontSize || "16px"};
  font-weight: ${({ fontWeight }) => fontWeight || "normal"};
  font-style: ${({ fontStyle }) => fontStyle || "normal"};
  text-align: ${({ textAlign }) => textAlign || "left"};
  align-content: ${({ alignContent }) => alignContent || "center"};
  line-height: ${({ lineHeight }) => lineHeight || 1.2}px;
  width: 100%;
  height: 100%;
  word-wrap: break-word;
  outline: none;
`;

/* Image Element */
const ImageElement = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: ${({ borderRadius }) => borderRadius || 0}px;
  border: ${({ borderWidth, borderColor }) => (borderWidth ? `${borderWidth}px solid ${borderColor}` : "none")};
  outline: none;
`;

const PosterBuilder = ({ data, setLoaderBox, setMessage }) => {
  const [posterData, setPosterData] = useState({});
  const [newFile, setNewFile] = useState(null);
  const [elements, setElements] = useState(data.posterDate ?? []);
  const [elementsUpdated, setElementsUpdated] = useState(false);
  const [elementCounter, setElementCounter] = useState(0);
  const [selectedElementId, setSelectedElementId] = useState(null);
  const [draggedLayerIndex, setDraggedLayerIndex] = useState(null);
  const [defaultWidth, setDefaultWidth] = useState(700);
  const [defaultHeight, setDefaultHeight] = useState(700);
  const [initialized, setInitialized] = useState(false);
  // const [bgBlobUrl, setBgBlobUrl] = useState(null);
  const [isDownloading, setIsDownloading] = useState(false);
  const [scale, setScale] = useState(10);

  // Store the initial state of elements
  const initialElements = useRef(elements);
  useEffect(() => {
    // Check if elements have changed compared to the initial state
    const hasChanged = JSON.stringify(elements) !== JSON.stringify(initialElements.current);
    setElementsUpdated(hasChanged);
  }, [elements]);

  // Function to discard changes
  const discardChanges = () => {
    setElements(initialElements.current); // Reset to initial state
    setElementsUpdated(false); // Reset the updated state
    setSelectedElementId(null); // Deselect any selected element
  };
  useEffect(() => {
    setElementsUpdated(true);
  }, [elements]);
  // useEffect(() => {
  //   // Cleanup function to revoke the object URL
  //   return () => {
  //     if (bgBlobUrl) {
  //       URL.revokeObjectURL(bgBlobUrl);
  //     }
  //   };
  // }, [bgBlobUrl]);
  const fetchPosterData = useCallback(async () => {
    try {
      setLoaderBox(true);
      const response = await getData({ id: data._id }, "advocacy-poster");
      const poster = response.data.data;
      const json = JSON.parse(poster.imageBulderData ?? "[]"); // Use '[]' as a fallback for JSON parsing
      setPosterData(poster);
      setElementCounter(json.length);
      setElements(json);
      setDefaultWidth(poster.layoutWidth);
      setDefaultHeight(poster.layoutHeight);
      // const iamgeResponse = await fetch(process.env.REACT_APP_CDN + poster.backgroundImage);
      // if (!iamgeResponse.ok) throw new Error("Network response was not ok");
      // const blob = await iamgeResponse.blob();
      // const blobUrl = URL.createObjectURL(blob);
      // setBgBlobUrl(blobUrl);
      setLoaderBox(false);
    } catch (error) {
      console.error("Error fetching poster data:", error);
      setLoaderBox(false);
    }
  }, [data._id, setLoaderBox]); // Add data._id as a dependency

  useEffect(() => {
    if (!initialized) {
      setInitialized(true);
      fetchPosterData();
    }
  }, [data, initialized, fetchPosterData]);
  // Handle file changes
  const handleFileChange = (e) => {
    const { files } = e.target;
    setNewFile(files);
  };

  // Handle input changes
  const handleInputChange = (name, value) => {
    setPosterData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Add new text element
  const addTextElement = () => {
    const newElement = {
      id: elementCounter,
      type: "text",
      content: "Sample Text",
      color: "#000000",
      fontSize: 16,
      fontWeight: "normal",
      fontStyle: "normal",
      textAlign: "left",
      alignContent: "start",
      lineHeight: 1.2,
      positionX: 0,
      positionY: 0,
      width: 200,
      height: 50,
      name: `name${elementCounter}`,
      label: `Text ${elementCounter}`,
    };
    setElements([...elements, newElement]);
    setElementCounter(elementCounter + 1);
  };

  // Add new image element
  const addImageElement = () => {
    const newElement = {
      id: elementCounter,
      type: "image",
      src: null, // Will be set when the user uploads an image
      positionX: 0,
      positionY: 0,
      width: 100,
      height: 100,
      borderRadius: 0,
      borderWidth: 0,
      borderColor: "#000000",
      name: `name${elementCounter}`,
      label: `Text ${elementCounter}`,
      isProfilePicture: false,
    };
    setElements([...elements, newElement]);
    setElementCounter(elementCounter + 1);
  };

  // Update element properties
  const updateElement = (id, updatedProperties) => {
    setElementsUpdated(true);
    setElements((prevElements) => prevElements.map((el) => (el.id === id ? { ...el, ...updatedProperties } : el)));
  };

  // Handle file upload for image elements
  const handleElementFileChange = (id, file) => {
    const url = URL.createObjectURL(file);
    updateElement(id, { src: url });

    // Revoke URL when component unmounts or image changes
    return () => {
      URL.revokeObjectURL(url);
    };
  };

  // Remove element
  const removeElement = (id) => {
    setElements((prevElements) => prevElements.filter((el) => el.id !== id));

    // If the removed element was selected, reset the selection
    if (selectedElementId === id) {
      setSelectedElementId(null);
    }
  };

  // Handle selection
  const handleElementClick = (id) => {
    setSelectedElementId(id);
  };

  // Handle arrow key movement
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (selectedElementId !== null) {
        const moveBy = e.shiftKey ? 10 : 1;
        const element = elements.find((el) => el.id === selectedElementId);
        if (!element) return;

        let newX = element.positionX;
        let newY = element.positionY;

        switch (e.key) {
          case "ArrowUp":
            newY -= moveBy;
            e.preventDefault();
            break;
          case "ArrowDown":
            newY += moveBy;
            e.preventDefault();
            break;
          case "ArrowLeft":
            newX -= moveBy;
            e.preventDefault();
            break;
          case "ArrowRight":
            newX += moveBy;
            e.preventDefault();
            break;
          default:
            return;
        }

        // Update the position of the selected element
        updateElement(selectedElementId, {
          positionX: newX,
          positionY: newY,
        });
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [selectedElementId, elements]);

  // Drag and Drop Handlers for Layers
  const handleDragStart = (index) => {
    setDraggedLayerIndex(index);
  };

  const handleDragOver = (index, e) => {
    e.preventDefault(); // Necessary to allow drop
  };

  const handleDrop = (index) => {
    if (draggedLayerIndex === null) return;

    const updatedElements = [...elements];
    const [movedElement] = updatedElements.splice(draggedLayerIndex, 1);
    updatedElements.splice(index, 0, movedElement);

    setElements(updatedElements);
    setDraggedLayerIndex(null);
  };
  const SaveData = async () => {
    setLoaderBox(true);
    if (checkValidSlug(posterData.slug)) {
      if (newFile) {
        await putData({ backgroundImage: newFile, id: data._id, imageBulderData: JSON.stringify(elements), layoutHeight: posterData.layoutHeight, slug: posterData.slug, layoutWidth: posterData.layoutWidth }, "advocacy-poster");
        fetchPosterData();
      } else {
        await putData({ id: data._id, imageBulderData: JSON.stringify(elements), layoutHeight: posterData.layoutHeight, slug: posterData.slug, layoutWidth: posterData.layoutWidth }, "advocacy-poster");
      }
    } else {
      setMessage({ content: "Please enter a valid slug", type: 1, icon: "error" });
    }
    setLoaderBox(false);
  };
  const checkValidSlug = (value) => {
    // Slug validation logic
    const slugRegex = /^[a-z0-9-]+$/;
    const isValid = slugRegex.test(value);

    return isValid;
  };
  const Download = async () => {
    try {
      setLoaderBox(true);
      const response = await getCustomData({ id: data._id }, "https://poster-builder-api.eventhex.ai/api/v1/user/download-poster");
      console.log(response);
      if (response.status === 200) {
        // Convert the base64 string to a Blob
        const base64 = response.data.response;
        const binaryData = atob(base64);
        const arrayBuffer = new ArrayBuffer(binaryData.length);
        const uint8Array = new Uint8Array(arrayBuffer);

        for (let i = 0; i < binaryData.length; i++) {
          uint8Array[i] = binaryData.charCodeAt(i);
        }

        const blob = new Blob([uint8Array], { type: "image/png" });

        // Create a download link and trigger the download
        const downloadLink = document.createElement("a");
        downloadLink.href = URL.createObjectURL(blob);
        downloadLink.download = "event-poster.png";
        document.body.appendChild(downloadLink);
        downloadLink.click();
        document.body.removeChild(downloadLink);
        setLoaderBox(false);
      } else {
        console.error("Error downloading poster:", response.message);
        setLoaderBox(false);
      }
    } catch (error) {
      console.error("Error downloading poster:", error);
      setLoaderBox(false);
    }
  };

  const posterRef = useRef(null); // Reference to the poster div

  // const Download = async () => {
  //   setIsDownloading(true);
  //   if (posterRef.current) {
  //     const canvas = await html2canvas(posterRef.current);
  //     const dataURL = canvas.toDataURL("image/png");
  //     // Create a download link and trigger the download
  //     const downloadLink = document.createElement("a");
  //     downloadLink.href = dataURL;
  //     downloadLink.download = "event-poster.png";
  //     document.body.appendChild(downloadLink);
  //     downloadLink.click();
  //     document.body.removeChild(downloadLink);
  //   }
  // };
  return (
    <Container>
      {/* Right Side - Display */}
      <Preview>
        <ControlsBox>
          <PageHeader line={false} title={"Poster Preview"} description={"Manage your poster, move, resize object in the poster!"}></PageHeader>{" "}
          <ControlsSection onMouseEnter={() => setIsDownloading(true)}>
            <SliderBox
              min={5}
              max={10}
              label="Scale Layout"
              value={scale}
              onChange={(e) => {
                console.log(e);
                setScale(parseFloat(e));
              }}
            ></SliderBox>
            <Button align="theme" icon={"add"} ClickEvent={() => addTextElement()} value="Add Text"></Button>
            <Button align="theme" icon={"add"} ClickEvent={() => addImageElement()} value="Add Image"></Button>
            <Button
              align="theme"
              icon={"download"}
              ClickEvent={() => {
                setIsDownloading(true);
                Download();
              }}
              value="Generate Sample"
            ></Button>
          </ControlsSection>
        </ControlsBox>
        {/* Selected Element Settings */}
        <Poster onMouseEnter={() => setIsDownloading(false)} ref={posterRef} width={posterData.layoutWidth || defaultWidth} height={posterData.layoutHeight || defaultHeight}>
          {posterData.backgroundImage && (
            <img
              style={{ position: "absolute", objectFit: "cover", width: "100%" }}
              alt="background"
              src={process.env.REACT_APP_CDN + posterData.backgroundImage}
              onLoad={() => {
                console.log("Background image loaded");
                // setIsImageLoaded(true);
              }}
            />
          )}
          {elements.map((element) => (
            <StyledRnd
              key={element.id}
              size={{
                width: element.width,
                height: element.height,
              }}
              position={{
                x: element.positionX,
                y: element.positionY,
              }}
              bounds="parent"
              onDragStart={() => handleElementClick(element.id)}
              onDragStop={(e, d) =>
                updateElement(element.id, {
                  positionX: d.x,
                  positionY: d.y,
                })
              }
              onResizeStart={() => handleElementClick(element.id)}
              onResizeStop={(e, dir, ref, delta, position) =>
                updateElement(element.id, {
                  width: ref.offsetWidth,
                  height: ref.offsetHeight,
                  positionX: position.x,
                  positionY: position.y,
                })
              }
              onClick={() => handleElementClick(element.id)}
              style={{
                border: isDownloading ? "2px dashed transparent" : selectedElementId === element.id ? "2px dashed blue" : "2px dashed rgb(240 240 240 / 14%)",
              }}
            >
              {element.type === "text" && (
                <TextElement color={element.color} fontSize={`${element.fontSize}px`} fontWeight={element.fontWeight} fontStyle={element.fontStyle} textAlign={element.textAlign} alignContent={element.alignContent} lineHeight={element.lineHeight}>
                  {element.content}
                </TextElement>
              )}
              {element.type === "image" && (
                <ImageElement
                  onError={(e) => {
                    e.target.src = noimage; // Hide the image on error
                  }}
                  src={element.src}
                  alt="Element Image"
                  borderRadius={element.borderRadius}
                  borderWidth={element.borderWidth}
                  borderColor={element.borderColor}
                />
              )}
            </StyledRnd>
          ))}
        </Poster>
        {selectedElementId !== null && (
          <ItemSettings className="nomargin">
            {/* <SubPageHeader title={"Settings"} description={"Manage Element"} /> */}
            {(() => {
              const element = elements.find((el) => el.id === selectedElementId);
              if (!element) return null;
              if (element.type === "text") {
                return (
                  <Form className="double plain horizontal">
                    {/* <TextBox align="full" label="Text Content" placeHolder={"Sample Text"} onChange={(e) => updateElement(element.id, { content: e })} value={element.content}></TextBox> */}
                    <NumberBox min={8} max={70} addValue={1} align="control" label="Font Size" placeHolder={"Font Size"} onChange={(e) => updateElement(element.id, { fontSize: Number(e) })} value={element.fontSize}></NumberBox>
                    <NumberBox min={8} max={70} addValue={1} align="control" label="Line Height" placeHolder={"Line Height"} onChange={(e) => updateElement(element.id, { lineHeight: Number(e) })} value={element.lineHeight}></NumberBox>
                    <NumberBox min={10} max={defaultWidth} align="control" label="Width" placeHolder={"Width"} onChange={(e) => updateElement(element.id, { width: Number(e) })} value={element.width}></NumberBox>
                    <NumberBox min={10} max={defaultHeight} align="control" label="Height" placeHolder={"Height"} onChange={(e) => updateElement(element.id, { height: Number(e) })} value={element.height}></NumberBox>
                    <ColorPicker align="control" label="Text Color" placeHolder={"Select Color"} onChange={(e) => updateElement(element.id, { color: e })} value={element.color}></ColorPicker>
                    <Select
                      key={`alignment-${selectedElementId}`}
                      align="control"
                      label="Hor. Align"
                      selectApi={[
                        { id: "left", value: "Left" },
                        { id: "center", value: "Center" },
                        { id: "right", value: "Right" },
                      ]}
                      value={element.textAlign}
                      onSelect={(e) => updateElement(element.id, { textAlign: e.id })}
                    ></Select>
                    <Select
                      key={`valignment-${selectedElementId}`}
                      align="control"
                      label="Ver. Align"
                      selectApi={[
                        { id: "start", value: "Top" },
                        { id: "center", value: "Center" },
                        { id: "end", value: "End" },
                      ]}
                      value={element.alignContent}
                      onSelect={(e) => updateElement(element.id, { alignContent: e.id })}
                    ></Select>
                    <Select
                      key={`font-weight-${selectedElementId}`}
                      align="control"
                      label="Font Weight"
                      selectApi={[
                        { id: "normal", value: "Normal" },
                        { id: "bold", value: "Bold" },
                      ]}
                      value={element.fontWeight}
                      onSelect={(e) => updateElement(element.id, { fontWeight: e.id })}
                    ></Select>

                    <Select
                      key={`font-style-${selectedElementId}`}
                      align="control"
                      label="Font Style"
                      selectApi={[
                        { id: "normal", value: "Normal" },
                        { id: "italic", value: "Italic" },
                      ]}
                      value={element.fontStyle}
                      onSelect={(e) => updateElement(element.id, { fontStyle: e.id })}
                    ></Select>
                  </Form>
                );
              } else if (element.type === "image") {
                return (
                  <Form className="double plain horizontal">
                    <NumberBox min={0} max={100} align="control" label="Radius" placeHolder={"Border Radius"} onChange={(e) => updateElement(element.id, { borderRadius: Number(e) })} value={element.borderRadius}></NumberBox>
                    <NumberBox min={0} max={5} align="control" label="Border Width" placeHolder={"Border Width"} onChange={(e) => updateElement(element.id, { borderWidth: Number(e) })} value={element.borderWidth}></NumberBox>
                    <NumberBox min={0} max={300} align="control" label="Width" placeHolder={"Width"} onChange={(e) => updateElement(element.id, { width: Number(e) })} value={element.width}></NumberBox>
                    <NumberBox min={0} max={300} align="control" label="Height" placeHolder={"Height"} onChange={(e) => updateElement(element.id, { height: Number(e) })} value={element.height}></NumberBox>
                    <ColorPicker align="control" label="Border Color" placeHolder={"Border Color"} onChange={(e) => updateElement(element.id, { borderColor: e })} value={element.borderColor}></ColorPicker>
                  </Form>
                );
              }
            })()}
          </ItemSettings>
        )}
        {/* <pre>
          <code>{JSON.stringify(elements, null, 2)}</code>
        </pre> */}
      </Preview>
      {/* Left Side - Controls */}
      <Controls>
        {/* Layers List */}
        <ItemSettings>
          <SubPageHeader title={"Layers"} description={"All elements added to the poster!"} />
          <LayersList>
            {elements.length > 0 ? (
              elements.map((element, index) => (
                <LayerItem key={element.id} isSelected={selectedElementId === element.id} draggable onDragStart={() => handleDragStart(index)} onDragOver={(e) => handleDragOver(index, e)} onDrop={() => handleDrop(index)} onClick={() => setSelectedElementId(element.id)}>
                  <div>{element.type === "text" ? element.content : element.type.charAt(0).toUpperCase() + element.type.slice(1) + ` Element ${index + 1}`}</div>
                  <IconButton
                    icon={"delete"}
                    ClickEvent={(e) => {
                      e.stopPropagation();
                      removeElement(element.id);
                    }}
                  ></IconButton>
                </LayerItem>
              ))
            ) : (
              <NoDataFound icon="layers" shortName={"layes"}></NoDataFound>
            )}
          </LayersList>
        </ItemSettings>
        {/* Add Elements */}

        {selectedElementId !== null && (
          <ItemSettings>
            <SubPageHeader title={"Content"} description={"Set your element content!"} />
            {(() => {
              const element = elements.find((el) => el.id === selectedElementId);
              if (!element) return null;
              if (element.type === "text") {
                return (
                  <Form className="double plain ">
                    <TextBox align="full" label="Set Label for Form" placeHolder={"Label in Form"} onChange={(e) => updateElement(element.id, { label: e })} value={element.label}></TextBox>
                    <TextBox align="full" label="Sample Content" placeHolder={"Sample Text"} onChange={(e) => updateElement(element.id, { content: e })} value={element.content}></TextBox>
                  </Form>
                );
              } else if (element.type === "image") {
                return (
                  <Form className="double plain ">
                    <TextBox align="full" label="Set Label for Form" placeHolder={"Label in Form"} onChange={(e) => updateElement(element.id, { label: e })} value={element.label}></TextBox>
                    <FormInput customClass="full" type="image" label={"Image Source"} formValues={{ old_backgroundImage: "" }} value={""} name={"backgroundImage"} onChange={(e) => handleElementFileChange(element.id, e.target.files[0])}></FormInput>
                  </Form>
                );
              }
            })()}
          </ItemSettings>
        )}
        <ItemSettings>
          {/* Background Image Upload */}
          <SubPageHeader title={"Layout Settings"} description={"Set the layout for the poster"} />
          <Form className="double plain">
            {/* <Label>Background Image</Label>
          <Input type="file" name="backgroundImage" accept="image/*" onChange={handleFileChange} /> */}
            <FormInput update={true} type="image" formType={"put"} label={"Background Image"} disabled={false} formValues={{ old_backgroundImage: posterData.backgroundImage, backgroundImage: posterData.backgroundImage }} value={"backgroundImageUrl"} name={"backgroundImage"} onChange={handleFileChange}></FormInput>

            <TextBox align="half" label="Width" placeHolder={"Width"} onChange={(e) => handleInputChange("layoutWidth", Number(e))} value={posterData.layoutWidth || defaultWidth}></TextBox>
            <TextBox align="half" label="Height" placeHolder={"Height"} onChange={(e) => handleInputChange("layoutHeight", Number(e))} value={posterData.layoutHeight || defaultHeight}></TextBox>
            <TextBox
              align="full"
              label="Slug"
              placeHolder={"Slug"}
              onChange={(e) => {
                handleInputChange("slug", e);
              }}
              value={posterData.slug}
            ></TextBox>
          </Form>
        </ItemSettings>
        {elementsUpdated && (
          <Footer className="embed popup">
            <FormInput type="close" value={"Discard Changes"} onChange={discardChanges} />
            <FormInput type="submit" name="submit" value={"Publish"} onChange={SaveData} />
          </Footer>
        )}
      </Controls>
    </Container>
  );
};

export default PosterBuilder;
