const ColorPicker = (props) => {
  const [primaryColour, setPrimaryColour] = useState("");
  const [colorShades, setColorShades] = useState({});
  const [showColorPicker, setShowColorPicker] = useState(false);
  const pickerRef = useRef(null);

  useEffect(() => {
    const fetchInitialColors = async () => {
      try {
        const response = await getData(
          { event: props.openData.data._id },
          "app-setting"
        );
        console.log(response);
        if (response.data.response.length > 0) {
          const colorData = response.data.response[0];
          setPrimaryColour(colorData.primaryColour);
          setColorShades({
            primaryDarker: colorData.primaryDarker,
            primaryDark: colorData.primaryDark,
            primaryBase: colorData.primaryBase,
            primaryLighter: colorData.primaryLighter,
            primaryLightest: colorData.primaryLightest,
          });
        }
      } catch (error) {
        console.error("Error fetching initial colors:", error);
      }
    };

    fetchInitialColors();
  }, [props.openData.data._id]);

  const generateShades = (color) => {
    const primaryDarker = chroma(color).darken(2.2).desaturate(0.3).hex();
    const primaryDark = chroma(color).darken(1).desaturate(0.1).hex();
    const primaryBase = chroma(color).hex();
    const primaryLightest = chroma(color).brighten(1.8).mix("white", 0.4).hex();
    const primaryLighter = chroma(color).brighten(2.2).mix("white", 0.6).hex();

    setColorShades({
      primaryDarker,
      primaryDark,
      primaryBase,
      primaryLighter,
      primaryLightest,
    });
  };

  const handlePrimaryColorChange = (color) => {
    console.log({ color });
    const selectedColor = color.hex;
    setPrimaryColour(selectedColor);
    generateShades(selectedColor);
  };

  const handleSave = async () => {
    try {
      const payload = {
        event: props.openData.data._id,
        primaryColour,
        primaryDarker: colorShades.primaryDarker || primaryColour,
        primaryDark: colorShades.primaryDark || primaryColour,
        primaryBase: colorShades.primaryBase || primaryColour,
        primaryLighter: colorShades.primaryLighter || primaryColour,
        primaryLightest: colorShades.primaryLightest || primaryColour,
      };
      const response = await postData(payload, "app-setting");
      if (response.status === 200) {
        props.setMessage({
          type: 1,
          content: "Color settings saved successfully!",
          proceed: "Okay",
          icon: "success",
        });
      }
    } catch (error) {
      console.error("Error saving color settings:", error);
    }
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (pickerRef.current && !pickerRef.current.contains(event.target)) {
        setShowColorPicker(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [pickerRef]);

  return (
    <Wrapper>
      <Items>
        <div
          style={{
            textAlign: "left",
            display: "flex",
            justifyContent: "left",
            fontSize: "large",
          }}
        >
          <p>Choose Color</p>
        </div>
        <div ref={pickerRef} style={{ width: "100%" }}>
          <input
            type="text"
            value={primaryColour}
            placeholder="Pick a Primary Color"
            readOnly
            onClick={() => setShowColorPicker(!showColorPicker)}
            style={{
              width: "100%",
              maxWidth: "230px",
              height: "40px",
              backgroundColor: primaryColour,
              border: "1px solid #ccc",
              cursor: "pointer",
              borderRadius: "5px",
              textAlign: "center",
            }}
          />
          {showColorPicker && (
            <div
              style={{ marginTop: "10px", width: "100%", maxWidth: "230px" }}
            >
              <SketchPicker
                color={primaryColour}
                onChangeComplete={handlePrimaryColorChange}
                onClose={() => setShowColorPicker(false)}
                styles={{
                  default: {
                    picker: {
                      width: "100%",
                    },
                  },
                }}
              />
            </div>
          )}
        </div>

        <BuildWrapper
          onClick={handleSave}
          style={{ marginTop: "10px", maxWidth: "230px" }}
        >
          Submit
        </BuildWrapper>

        {primaryColour && (
          <div style={{ width: "100%", maxWidth: "400px" }}>
            <ColorDisplay>
              <ColorBox style={{ backgroundColor: primaryColour }} />
              <ColorInfo>
                <ColorLabel>primaryColour</ColorLabel>
                <ColorValue>{primaryColour}</ColorValue>
              </ColorInfo>
            </ColorDisplay>
            {Object.entries(colorShades).map(([name, color]) => (
              <ColorDisplay key={name}>
                <ColorBox style={{ backgroundColor: color }} />
                <ColorInfo>
                  <ColorLabel>{name}</ColorLabel>
                  <ColorValue>{color}</ColorValue>
                </ColorInfo>
              </ColorDisplay>
            ))}
          </div>
        )}
      </Items>

      <PreviewContainer>
        <PhonePreview>
          <div
            style={{
              background: primaryColour || "#0e73c5",
              minHeight: "180px",
              position: "relative",
              overflow: "hidden",
            }}
          >
            <div
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                background:
                  "linear-gradient(120deg, rgba(255,255,255,0.1), rgba(255,255,255,0))",
                zIndex: 1,
              }}
            />

            <Header>
              <WelcomeSection>
                <img
                  src={process.env.REACT_APP_CDN + props.openData.data.logo}
                  alt="user"
                  style={{
                    width: "38px",
                    height: "38px",
                    borderRadius: "50%",
                    border: "2px solid rgba(255,255,255,0.8)",
                    boxShadow: "0 2px 8px rgba(0,0,0,0.15)",
                  }}
                />
                <div style={{ color: "white" }}>
                  <div style={{ fontSize: "13px", opacity: 0.9 }}>
                    Welcome Back
                  </div>
                  <div style={{ fontSize: "17px", fontWeight: "600" }}>
                    {props.openData.data.title}
                  </div>
                </div>
              </WelcomeSection>
              <div style={{ display: "flex", gap: "12px", marginTop: "2px" }}>
                <div style={{ position: "relative" }}>
                  <MessageCircle size={22} color="white" strokeWidth={2.5} />
                  <NotificationBadge>4</NotificationBadge>
                </div>
                <div style={{ position: "relative" }}>
                  <Bell size={22} color="white" strokeWidth={2.5} />
                  <NotificationBadge>2</NotificationBadge>
                </div>
              </div>
            </Header>

            <div style={{ padding: "0 16px" }}>
              <EventBanner>
                <img
                  src={process.env.REACT_APP_CDN + props.openData.data.banner}
                  alt="Event Banner"
                  style={{
                    width: "100%",
                    height: "100%",
                    objectFit: "cover",
                    position: "absolute",
                    top: 0,
                    left: 0,
                    zIndex: 1,
                  }}
                />

                <div
                  style={{
                    position: "absolute",
                    bottom: "12px",
                    left: "12px",
                    zIndex: 3,
                    display: "flex",
                    alignItems: "center",
                    gap: "6px",
                    color: "rgba(255,255,255,0.9)",
                    fontSize: "12px",
                    fontWeight: "500",
                  }}
                >
                  <MapPin size={14} color="white" />
                  {props.openData.data.venue}
                </div>
              </EventBanner>
            </div>
          </div>

          <MenuGrid>
            <MenuItem>
              <Calendar
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Programs</span>
            </MenuItem>
            <MenuItem>
              <Mic
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Speakers</span>
            </MenuItem>
            <MenuItem>
              <Users
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Meetings</span>
            </MenuItem>
            <MenuItem>
              <MapPin
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Location</span>
            </MenuItem>
            <MenuItem>
              <Info
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>About us</span>
            </MenuItem>
            <MenuItem>
              <Image
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Photos</span>
            </MenuItem>
            <MenuItem>
              <Link
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Connections</span>
            </MenuItem>
            <MenuItem>
              <Store
                size={22}
                color={primaryColour || "#0e73c5"}
                strokeWidth={2}
              />
              <span>Exhibitors</span>
            </MenuItem>
          </MenuGrid>

          <EventNotification>
            <div
              style={{
                background: "#FFF6E9",
                padding: "8px",
                borderRadius: "10px",
              }}
            >
              <Bell size={22} color="#FFB547" strokeWidth={2} />
            </div>
            <div>
              <div style={{ fontWeight: "500", fontSize: "14px" }}>
                Your next event is on going
              </div>
              <div
                style={{ fontSize: "12px", color: "#666", marginTop: "2px" }}
              >
                Get closer to your schedule
              </div>
            </div>
            <div
              style={{
                marginLeft: "auto",
                background: primaryColour || "#0e73c5",
                width: "28px",
                height: "28px",
                borderRadius: "50%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                boxShadow: "0 2px 6px rgba(0,0,0,0.15)",
              }}
            >
              <span style={{ color: "white", fontSize: "18px" }}>→</span>
            </div>
          </EventNotification>

          <EntryPass color={primaryColour || "#0e73c5"}>
            <div
              style={{
                padding: "16px",
                borderLeft: "2px dashed rgba(255,255,255,0.2)",
                borderRadius: "12px",
                background:
                  "linear-gradient(45deg, rgba(255,255,255,0.1), transparent)",
                display: "flex",
                alignItems: "center",
                gap: "12px",
                width: "100%",
              }}
            >
              <img
                src="/api/placeholder/40/40"
                alt="User"
                style={{
                  width: "40px",
                  height: "40px",
                  borderRadius: "50%",
                  border: "2px solid rgba(255,255,255,0.8)",
                }}
              />
              <div>
                <div style={{ fontSize: "16px", fontWeight: "600" }}>
                  Safari Sanders
                </div>
                <div
                  style={{ fontSize: "12px", opacity: 0.8, marginTop: "2px" }}
                >
                  Project Manager
                </div>
              </div>
              <img
                src="/api/placeholder/80/80"
                alt="QR Code"
                style={{
                  width: "70px",
                  height: "70px",
                  marginLeft: "auto",
                  background: "white",
                  padding: "8px",
                  borderRadius: "8px",
                }}
              />
            </div>
          </EntryPass>

          <BottomNav>
            <NavItem active style={{ color: primaryColour || "#0e73c5" }}>
              <Home size={20} strokeWidth={2.2} />
              <span>Home</span>
            </NavItem>
            <NavItem>
              <Calendar size={20} strokeWidth={2.2} />
              <span>Sessions</span>
            </NavItem>
            <NavItem>
              <Camera size={20} strokeWidth={2.2} />
              <span>Photos</span>
            </NavItem>
            <NavItem>
              <MessageCircle size={20} strokeWidth={2.2} />
              <span>Networking</span>
            </NavItem>
            <NavItem>
              <Bell size={20} strokeWidth={2.2} />
              <span>Updates</span>
            </NavItem>
          </BottomNav>
        </PhonePreview>
      </PreviewContainer>
    </Wrapper>
  );
};

export default ColorPicker;
